import {Component, OnInit} from '@angular/core';
import {RequestOptions, URLSearchParams} from '@angular/http';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {ConfigsService} from 'app/core/configs.service';
import {HttpService} from 'app/core/services/http.service';

@Component({
	selector: 'app-house-list',
	templateUrl: './houses.component.html',
	styleUrls: ['./houses.component.scss']
})
export class HouseListComponent implements OnInit {
	data: any;
	analytics: boolean;

	constructor(private configs: ConfigsService,
				private httpService: HttpService) {
		this.analytics = false;
	}

	ngOnInit() {
		const self = this;
		const searchParams = new URLSearchParams();

		searchParams.set('owner', localStorage.token);

		const options = new RequestOptions({
			headers: this.configs.headersDefault,
			search: searchParams
		});

		this.httpService.get(this.configs.urlRequest.getCoffeeHouse, options)
			.subscribe(response => {
				console.log('Response', response);
				self.data = response;
			});
	}

}



