import {Component, Input, OnInit} from '@angular/core';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {AddedCoffeeHouseComponent} from 'app/core/dialogs/added-coffee-house/added-coffee-house.component';

@Component({
	selector: 'app-item-edit-btns',
	templateUrl: './item-edit-btns.component.html',
	styleUrls: ['./item-edit-btns.component.scss']
})
export class ItemEditBtnsComponent implements OnInit {

	@Input() item;

	constructor(private modalService: NgbModal) {}

	ngOnInit() {
	}

	edit() {
		const modalRef = this.modalService.open(AddedCoffeeHouseComponent);
		modalRef.componentInstance.item = this.item;
	}

}



