import {Component, OnInit} from '@angular/core';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {Router} from '@angular/router';
import {HttpService} from '../../core/services/http.service';

@Component({
	selector: 'app-analytics',
	templateUrl: './analytics.component.html',
	styleUrls: ['./analytics.component.scss']
})

export class AnalyticsComponent implements OnInit {

	data: any;
	analytics: boolean;
	name: any;
	isChecked = {};
	idCard: any;

	constructor(private globalData: GlobalDataService,
				private router: Router,
				private httpService: HttpService) {
		this.analytics = true;
	}

	ngOnInit() {
		this.httpService.get('coffeeHouse/')
			.subscribe(
				(res) => {
					console.log('Res', res);
					this.data = res;
				}
			);
		// this.data = this.globalData.getGlobalData();
		// console.log('Houses', this.data);
	}

	checkedHouses(ev) {
		console.log('Houses', ev);
		this.idCard = ev;
	}

	gooAnalyticsDetail(house) {
		console.log(house);
		if (this.idCard) {
			console.log('IdCardds', this.idCard);
			this.router.navigate(['pages/analytics/detail', {idCards: JSON.stringify(this.idCard)}]);
		} else {
			this.router.navigate(['pages/analytics/detail']);
		}
	}
}



