import {Component, OnInit} from '@angular/core';
import {HttpService} from 'app/core/services/http.service';
import {ActivatedRoute, Params} from '@angular/router';
import {ConfigsService} from 'app/core/configs.service';
import {ToastrService} from 'ngx-toastr';
// import {Socket} from 'ng-socket-io';
import * as io from 'socket.io-client';

@Component({
	selector: 'app-super-admin-users',
	templateUrl: './super-admin-users.component.html',
	styleUrls: ['./super-admin-users.component.scss']
})
export class SuperAdminUsersComponent implements OnInit {

	users: any;
	private socket;
	coffeeHouseId: string;

	constructor(private httpService: HttpService,
				public config: ConfigsService,
				private activatedRoute: ActivatedRoute,
				private toastrService: ToastrService) {
	}

	getUsers() {
		this.httpService.get(this.config.urlRequest.users)
			.subscribe(response => {
				console.log('Response', response);
				// return response;
				this.users = response;
			});
	}

	createAdminRequest(userId) {
		const requestData = {
			coffeeHouseID: this.coffeeHouseId, // '59c9d506ce0e011b6d53d0c9',
			userID: userId
		};

		this.httpService.post(this.config.urlRequest.adminRequest, requestData)
			.subscribe(response => {
				console.log(response);
				this.toastrService.success('Request sent!');
			});
	}

	ngOnInit() {
		const token = localStorage.getItem('Authorization');
		this.socket = io.connect('http://localhost:8030/', {
			query: 'token=' + token.slice(4, token.length)
		});

		this.activatedRoute.params.subscribe((params: Params) => {
			if (params.id) {
				this.coffeeHouseId = params.id;
				console.log('CoffeeHouse ID', params.id);
				this.socket.emit('inCoffeeHouse', params.id);  // '59c9d506ce0e011b6d53d0c9'
			}
		});

		this.socket.on('listOfCurrentVisitors', (users) => {
			this.users = users;
		});

		this.socket.on('newUserInCoffeeHouse', (user) => {
			this.users.push(user);
		});

		this.socket.on('userLeaveCoffeeHouse', (id) => {
			const index = this.users.findIndex((item) => {
				return item._id === id;
			});

			if (index !== -1) {
				this.users.splice(index, 1);
			}
		});
		// this.socket.emit('hello', 'Socet message!!!!!!!!!!!!!!!!!!!!!!!!!!');
		// this.socket.on('world', (message) => {
		// 	console.log(message);
		// });
	}

}



