import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {NgModule} from '@angular/core';
import {HttpModule} from '@angular/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';

// Firabase imports
import {AngularFireModule} from 'angularfire2';
import {AngularFirestoreModule} from 'angularfire2/firestore';
import {AngularFireAuthModule} from 'angularfire2/auth';
import {AngularFireDatabaseModule} from 'angularfire2/database';

// Module imports
import {AppRoutingModule} from './app-routing.module';
import {ImageCropperModule} from 'ng2-img-cropper';
import {NgbModule, NgbModal, NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {MatButtonModule, MatDialogModule, MatInputModule} from '@angular/material';
import {MatButtonToggleModule} from '@angular/material/button-toggle';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatAutocompleteModule} from '@angular/material/autocomplete';

import {NgbModalStack} from '@ng-bootstrap/ng-bootstrap/modal/modal-stack';
import {ToastrModule} from 'ngx-toastr';
import {OrderModule} from 'ngx-order-pipe';
import {Ng2FilterPipeModule} from 'ng2-filter-pipe';

// Service imports
import {ConfigsService} from './core/configs.service';
import {GlobalDataService} from './core/services/global-data.service';
import {LogoutService} from './core/services/logout.service';
import {GuardRoutingGuard} from 'app/core/guards/guard-routing.guard';
import {HttpService} from 'app/core/services/http.service';
import {GetUserFirebaseService} from 'app/core/services/get-user-firebase.service';
import {TriggerDashboardService} from 'app/core/services/trigger-dashboard.service';

// Component imports
import {AppComponent} from './app.component';
import {environment} from '../environments/environment';
import {PagesComponent} from './pages/pages.component';
import {HouseItemComponent} from './core/components/house-item/house-item.component';
import {HouseListComponent} from './pages/houses/houses.component';
import {ItemEditBtnsComponent} from './core/components/item-edit-btns/item-edit-btns.component';
import {ItemCheckboxComponent} from './core/components/item-checkbox/item-checkbox.component';
import {EditPopupComponent} from './core/modals/edit-popup/edit-popup.component';
import {EdditPhotoComponent} from './core/modals/eddit-photo/eddit-photo.component';
import {EdditSecondaryPhotosComponent} from './core/modals/eddit-secondary-photos/eddit-secondary-photos.component';
import {LeftBarComponent} from './core/components/left-bar/left-bar.component';
import {UserLeftBarComponent} from './core/components/user-left-bar/user-left-bar.component';
import {HeaderComponent} from './core/components/header/header.component';
import {HouseEditComponent} from './pages/house-edit/house-edit.component';
import {GoogleChartComponent} from './core/components/google-chart/google-chart.component';
import {AddedCoffeeHouseComponent} from './core/dialogs/added-coffee-house/added-coffee-house.component';

// page imports
import {AnalyticsComponent} from 'app/pages/analytics/analytics.component';
import {AnalyticsDetailComponent} from 'app/pages/analytics-detail/analytics-detail.component';
import {PaymentComponent} from 'app/pages/payment/payment.component';
import {ContactComponent} from 'app/pages/contact/contact.component';
import {LoginComponent} from 'app/pages/login/login.component';
import {SuperAdminOwnersComponent} from 'app/pages/super-admin-owners/super-admin-owners.component';
import {SuperAdminHousesComponent} from 'app/pages/super-admin-houses/super-admin-houses.component';
import {SuperAdminUsersComponent} from 'app/pages/super-admin-users/super-admin-users.component';
import {SuperAdminPayplanComponent} from 'app/pages/super-admin-payplan/super-admin-payplan.component';
import {SuperHouseComponent} from 'app/pages/super-house/super-house.component';
import {AllOwnersComponent} from './pages/all-owners/all-owners.component';
import {OwnerComponent} from './pages/owner/owner.component';
import {StatusCaffeePipe} from './core/pipes/status-caffee.pipe';


@NgModule({
	declarations: [
		AppComponent,
		PagesComponent,
		HouseItemComponent,
		HouseListComponent,
		ItemEditBtnsComponent,
		ItemCheckboxComponent,
		EditPopupComponent,
		EdditPhotoComponent,
		EdditSecondaryPhotosComponent,
		LeftBarComponent,
		UserLeftBarComponent,
		HeaderComponent,
		HouseEditComponent,
		GoogleChartComponent,
		AnalyticsComponent,
		AnalyticsDetailComponent,
		PaymentComponent,
		ContactComponent,
		LoginComponent,
		SuperAdminOwnersComponent,
		SuperAdminHousesComponent,
		SuperAdminUsersComponent,
		SuperAdminPayplanComponent,
		SuperHouseComponent,
		AddedCoffeeHouseComponent,
		AllOwnersComponent,
		OwnerComponent,
		StatusCaffeePipe
	],
	imports: [
		BrowserModule,
		AppRoutingModule,
		HttpModule,
		FormsModule,
		HttpClientModule,
		AngularFireModule.initializeApp(environment.firebase),
		AngularFirestoreModule,
		AngularFireAuthModule,
		AngularFireDatabaseModule,
		ImageCropperModule,
		NgbModule,
		BrowserAnimationsModule,
		MatButtonModule,
		MatDialogModule,
		MatInputModule,
		ReactiveFormsModule,
		ToastrModule.forRoot(environment.toastr),
		OrderModule,
		Ng2FilterPipeModule,
		MatProgressSpinnerModule,
		MatButtonToggleModule,
		MatAutocompleteModule,
		MatFormFieldModule
	],
	providers: [
		ConfigsService,
		GlobalDataService,
		LogoutService,
		GuardRoutingGuard,
		ReactiveFormsModule,
		HttpService,
		GetUserFirebaseService,
		NgbModal,
		NgbActiveModal,
		TriggerDashboardService,
		NgbModalStack
	],
	bootstrap: [
		AppComponent,
	],
	entryComponents: [
		AddedCoffeeHouseComponent
	]
})
export class AppModule {
}



