import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';

@Component({
	selector: 'app-all-owners',
	templateUrl: './all-owners.component.html',
	styleUrls: ['./all-owners.component.scss']
})
export class AllOwnersComponent implements OnInit {

	data: any;
	private authHeader: any;

	constructor(private httpService: HttpService,
				private configs: ConfigsService,
				public router: Router,
				private activatedRoute: ActivatedRoute) {
	}

	ngOnInit() {
		const self = this;
		this.authHeader = localStorage.getItem('Authorization');

		console.log('Init all-owners');

		this.httpService.get(this.configs.urlRequest.getOwners, {
			headers: {
				'Authorization': this.authHeader
			}
		})
			.subscribe(data => {
				self.data = data;
			});
	}

	removeCoffeeHouse(item) {
		const id = item._id;
		const self = this;
		console.log(id);

		return this.httpService.post(this.configs.urlRequest.removeCoffeeHouse + id)
			.subscribe(data => {
				console.log(data);
				item.remove();
			});
	}

}



