import {Component, OnInit} from '@angular/core';
import {GlobalDataService} from 'app/core/services/global-data.service';
import {HttpService} from 'app/core/services/http.service';
import {ConfigsService} from 'app/core/configs.service';

@Component({
	selector: 'app-super-admin-houses',
	templateUrl: './super-admin-houses.component.html',
	styleUrls: ['./super-admin-houses.component.scss']
})
export class SuperAdminHousesComponent implements OnInit {
	data: any;

	constructor(private httpService: HttpService, private configs: ConfigsService) {
	}

	ngOnInit() {
		const self = this;

		this.httpService.get(this.configs.urlRequest.getCoffeeHouse, {
			headers: {
				'Authorization': localStorage.getItem('Authorization')
			}
		})
			.subscribe(data => {
				console.log(data);
				self.data = data;
			});
	}
}



